.resume-links { 

    display: flex;
}

.resume-links div {
    margin : auto;
}

.resume-links a {
    text-decoration: none;
    color: inherit;
}

.resume-links a:hover {
    transition: .5s ease-in-out;
    opacity: .9;
    color: brown;
}

.resume-links a span{
    font-size: 20px;
}